import MSelect from 'material-components-vue/dist/select/select.min.js'
import MList from 'material-components-vue/dist/list/list.min.js'
import { types as questionTypes } from '../../../../question'

Vue.use(MSelect)
Vue.use(MList)

export default {
  name: 'TypeSelector',
  data () {
    return {
      value_: this.value,
      questionTypes,
    }
  },
  props: {
    value: String,
    texts: Object,
  },
  watch: {
    value (val) {
      this.value_ = val
    },
    value_ (val) {
      this.$emit('input', val)
    },
  },
}
